import { useState } from "react"
import S3 from 'react-aws-s3';
import axios from "axios"
import Loader from "./Loader";


function AddVideo(){

    const config = {
        bucketName: "lekhisahabbucket",
        region: "us-west-1",
        accessKeyId: "AKIA2BGS4LKF6M766XOI",
        secretAccessKey:"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",
    }

    var [videodata,setVideodata] =  useState({})
    var [videofile,setVideofile] =  useState()
    var [isloading,setIsloading] =  useState(false)
    var [thumbnail,setThumbnail] =  useState()

    function uploadVideo(){
        const ReactS3Client = new S3(config);
        setIsloading(true)
        // the name of the file uploaded is used to upload it to S3
        ReactS3Client
        .uploadFile(videofile, videofile.name)
        .then(data => {
            setIsloading(false)
        console.log(data.location)
        videodata.url = data.location
        setVideodata(videodata)
        })
        .catch(err =>{
            setIsloading(false)
             console.error(err)
            })
    }

    function uploadThumbnail(){
        const ReactS3Client = new S3(config);
        setIsloading(true)

        // the name of the file uploaded is used to upload it to S3
        ReactS3Client
        .uploadFile(thumbnail, thumbnail.name)
        .then(data => {
            setIsloading(false)
            console.log(data.location)
        videodata.thumbnail = data.location
        setVideodata(videodata)
        })
        .catch(err => {console.error(err)
            setIsloading(false)
        })

    }
    function handleTitle(e){
        videodata.title = e.target.value
        setVideodata(videodata)
    }

    function handleThumbnail(e){
        setThumbnail(e.target.files[0])
    }

    function handleVideo(e){
        console.log("....." , e.target.files[0])
        setVideofile(e.target.files[0])
    }

    function handleGenre(e){
        videodata.genre = e.target.value
        setVideodata(videodata)

    }

    function handleRating(e){
        videodata.rating = e.target.value
        setVideodata(videodata)

    }
    function handleDescription(e){
        videodata.description = e.target.value
        setVideodata(videodata)

    }

    function handleLanguages(e){
        videodata.languages = e.target.value
        setVideodata(videodata)
    }
    function addVideo(){
        videodata.languages = videodata.languages?.split(",")
        videodata.genre = videodata.genre?.split(",")
        console.log("Video data befor api hit is" , videodata)
        setIsloading(true)
        axios({
            method:"post",
            url:"http://localhost:5000/video/addvideo",
            data:videodata,
            headers:{
                "Authorization":localStorage.token
            }
        }).then((response)=>{
            console.log("Response from adding video to db", response)
        }).catch((error)=>{
            console.log("error in adding video to api", error)
        })

    }
    

    return (
        <>
        {isloading && <Loader />}
        <div className="container">
            <h1>Add Video</h1>
            <div className="form-group">
                <label>Upload Video File</label>
                <input onChange={handleVideo} type="file"></input>
                <button onClick={uploadVideo} className="btn btn-primary">Upload Video</button>
            </div>
            <div className="form-group">
                <label>Upload Thumbnail File</label>
                <input onChange={handleThumbnail} type="file"></input>
                <button onClick={uploadThumbnail} className="btn btn-primary"> Upload Thumbnail</button>
            </div>
            <div className="form-group">
                <label>Title</label>
                <input onChange={handleTitle} className="form-control"></input>
            </div>
            <div className="form-group">
                <label>Description</label>
                <textarea onChange={handleDescription} className="form-control"></textarea>
            </div>
            <div className="form-group">
                <label>Genre</label>
                <input onChange={handleGenre} placeholder="Genere (Comma Seperated)" className="form-control"></input>
            </div>
            <div className="form-group">
                <label>Languages</label>
                <input onChange={handleLanguages} placeholder="Languages (Comma Seperated)" className="form-control"></input>
            </div>
            <div className="form-group">
                <label>Rating</label>
                <input onChange={handleRating} type="number"  className="form-control"></input>
            </div>
            <div>
                <button onClick={addVideo} className="btn btn-primary">Add Video</button>
            </div>
        </div>
        </>
    )

}

export default AddVideo