import axios from "axios"
import { useEffect, useState } from "react"
import { BrowserRouter, Routes, Route } from "react-router-dom"
import AddVideo from "./Addvideo"
import Admin from "./Admin"
import Browse from "./Browse"
import Signupstateful from "./Classsignup"
import Forgot from "./Forgot"
import Home from "./Home"
import Login from "./Login"
import Navbar from "./Navbar"
import Pagenotfound from "./Pagenotfound"
import Playvideo from "./Playvideo"
import Profile from "./Profile"
import Signup from "./Signup"


function MyRouter(props) {   
  var [isloggedin,setIsloggedin] = useState(localStorage.token?true:false)

   useEffect(()=>{
    axios({
        method:"get",
        url:"http://localhost:5000/user/acquireloginstatus",
        headers:{
            authorization:localStorage.token
        }
      }).then((response)=>{
        setIsloggedin(true)

      }).catch((error)=>{
            setIsloggedin(false)
      })
   },[])

  function logindone(){
    setIsloggedin(true)
  }

    return (
        <>
            <BrowserRouter>
                <Navbar isloggedin={isloggedin} title="Video Streaming App" />
                <Routes>
                   <Route path="/" element={<Home isloggedin={isloggedin} logindone={logindone} />} /> 
                    <Route path="/profile" element={<Profile />} />
                    <Route path="/addvideo" element={<AddVideo/>} />
                    <Route path="/watch/:videoid" element={<Playvideo  isloggedin={isloggedin} />} />
                    <Route path="/browse" element={<Browse/>} />
                    <Route path="/admin" element={<Admin />} />
                    <Route path="/login" element={<Login logindone={logindone} />} />
                    <Route path="/forgot" element={<Forgot />} />
                    <Route path="/signup" element={<Signup />} />
                    <Route path="/*" element={<Pagenotfound />} />
                </Routes>
            </BrowserRouter>
        </>
    )
}

export default MyRouter