import React from "react"
import User from "./User"
var users = [{
    name: "Ashu Lekhi",
    image: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRRkZPkFU84BngelHjiRxudV7ZF0kDS1MCnaMnWAiSDsA&s",
    role: "Trainer"
}, { name: "Bharat", role: "SDE1", image: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSkQhTrpfUpotL28Ew-Q8bpqXPr9mJRR7rbb-YTkevq&s" }]

class Signupstateful extends React.Component {
    constructor() {
        super()
        this.state = {
            error: null
        }
    }
    getStarted = () => {
        this.setState({
            error: "Email is Required"
        })
    }
    render() {
        return (
            <>
                 {users.map((each)=>{
                    return <User data={each} />
                 })}
                <input className="form-control"></input>
                {this.state.error && <label className="alert alert-warning">{this.state.error}</label>}
                <button onClick={this.getStarted} className="btn btn-danger">Get Started</button>
            </>
        )
    }
}
export default Signupstateful