import axios from "axios"
import {useState} from "react"
function Forgot() {
  var [message,setMessage] = useState(null)
  var [error,setError] = useState(null)
   var request = {}
   function getEmail(e){
    setMessage(null)
    setError(null)
        request.email = e.target.value
   } 

   function recover(){
    if(request.email){
        axios({
            method:"post",
            data:request,
            url:"http://localhost:5000/user/recoverpassword"
        }).then((response)=>{
            setMessage("Password Sent to your email Successfuly! ")
        }).catch((error)=>{
            console.log("error from api is", error)
            if(error?.response?.data?.message){
                setError("No Such Email Exists!!")
            }
            else{
                setError("Internal Server Error")

            }
        })
    }
    else{
        alert("Please enter email first")
    }
   
   }

    return (
        <>
            <div className="container">
                <h1>Recover Password Here </h1>
                <div className="row">
                    <div className="col-8">
                        <input onChange={getEmail} placeholder="Enter Your Email" className="form-control"></input>

                    </div>
                    <div className="col-4">
                        <button onClick={recover} className="btn btn-primary">Submit</button>
                    </div>
                </div>
                {message && <div className="alert alert-success">
                    {message}
                </div>}
                {error && <div className="alert alert-danger">
                    {error}
                </div>}

            </div>

        </>
    )
}

export default Forgot