import axios from "axios"
import { useState } from "react"
import { useNavigate } from "react-router"
import Loading from "./Loader"

function Login(props) {
  var navigate = useNavigate()
  var [isloading,setIsloading] =  useState(false)
  var [user, setUser] = useState({})
  function handleEmail(e) {
    user.email = e.target.value
    setUser(user)
  }
  function handlePassword(e) {
    user.password = e.target.value
    setUser(user)
  }

  function login(e) {
    setIsloading(true)
    axios({
      url: "http://localhost:5000/user/login",
      data: user,
      method: "post"
    }).then((response) => {
      console.log("response from login api", response, response.headers.authorization)
      localStorage.token = response.headers.authorization
      props.logindone()
      setIsloading(false)

      if (response.data.user.profilecompleted) {
        navigate("/")
      }
      else {
        navigate("/profile")
      }
    }).catch((error) => {
      setIsloading(false)

    })
  }

  return <div>
    {isloading && <Loading />}
    <div className="row">
      <div className="col">
        <img style={{ margin:"2rem", height:"60%", width: "100%" }} src="home.webp"></img>
      </div>
      <div className="col">
        <div style={{ margin: "auto", width: "50%" }}>
          <div className="form-group">
            <h1>Login Here</h1>
            <label>Email</label>
            <input onChange={handleEmail} className="form-control"></input>
          </div>
          <div className="form-group">
            <label>Password</label>
            <input onChange={handlePassword} type="password" className="form-control"></input>
          </div>
          <button onClick={login} className="btn btn-primary">Login</button>
        </div>

      </div>
    </div>

  </div>
}

export default Login 