import { useState } from "react"
import axios from "axios"
import S3 from 'react-aws-s3';

// installed using npm install buffer --save
window.Buffer = window.Buffer || require("buffer").Buffer;

function Profile() {
    var [imgsrc, setImgstrc] = useState(null)
    var [file, setFile] = useState()
    var [imageurl, setImageurl] = useState()
    var [name,setName] =  useState()

    const config = {
        bucketName: "lekhisahabbucket",
        region: "us-west-1",
        accessKeyId: "AKIA2BGS4LKF6M766XOI",
        secretAccessKey: "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",
    }



    const uploadFile = async () => {
        const ReactS3Client = new S3(config);
        // the name of the file uploaded is used to upload it to S3
        ReactS3Client
            .uploadFile(file, file.name)
            .then(data => {console.log(data.location)
            setImageurl(data.location)})
            .catch(err => console.error(err))
    }

    function save(){
       var requestobj = {
        name:name,
        image:imageurl
       }
       axios({
        method:"put",
        url:"http://localhost:5000/user/updateprofile",
        data:requestobj,
        headers:{
            "Authorization":localStorage.token
        }
       })
    }


    function getFile(e) {
        setFile(e.target.files[0])


        setImgstrc(URL.createObjectURL(e.target.files[0]))

    }

    function handleName(e) {
       setName(e.target.value)
    }
    return (
        <div>
            <h1>Complete Your Profile</h1>
            <div className="form-group">
                <label>Profile Image:</label>
                <input accept="image/*" onChange={getFile} type="file"></input>
                <button onClick={uploadFile} className="btn btn-primary">Upload</button>
                {imgsrc && <img style={{height:"20rem",width:"20rem"}} src={imgsrc} />}
            </div>
            <div className="form-group">
                <label>Name</label>
                <input onChange={handleName} className="form-control"></input>
            </div>
            <button onClick={save} className="btn btn-primary">Save</button>
        </div>
    )
}

export default Profile