import { useNavigate } from "react-router"

function Video(props) {
    var videodata = props.data 
    var navigate = useNavigate()
    function playVideo(){
        var url = "/watch/"+props.data.videoid
        navigate(url)
    }
    return (
        <div class="card" style={{width: "18rem"}}>
            <img onClick={playVideo} src={videodata.thumbnail} class="card-img-top" alt="..." />
                <div class="card-body">
                    <h5 class="card-title">{videodata.title}</h5>
                    <p class="card-text">{videodata.description}</p>
                </div>
        </div>
    )
}

export default Video