import axios from "axios"

function Signup(){
    var user = {}
    function register(){
        if(user.email && user.password){
            console.log("user entered" , user)
            // make api call 
            axios({
                url:"http://localhost:5000/user/register",
                method:"post",
                data:user
            }).then(function(response){
                console.log("response from api is" , response)
            }).catch(function(error){
                console.log("Error from api is" , error)
            })
        }
        else{
            alert("Please fill details first")
        }
       
    }
    
    function getPassword(event){
        user.password = event.target.value
    }

    function getEmail(event){
        user.email = event.target.value
       console.log("value is" , event.target.value)
    }
    return <div className="container">
        <div className="col-md-4">
            <h1>Signup Here</h1>
            <div className="form-group">
                <label>Email</label>
                <input onChange={getEmail} className="form-control"></input>
            </div>
            <div className="form-group">
                <label>Password</label>
                <input onChange={getPassword} type="password" className="form-control"></input>
            </div>
            <button onClick={register} className="btn btn-primary">Signup</button>
        </div>
       
    </div>
}

export default Signup