import { Link } from "react-router-dom"
function Navbar(props) {


  function logout() {
    localStorage.clear()
    window.location.href = "/"
  }
  console.log("Props received", props)
  return (
    <nav class="navbar navbar-expand-lg bg-light">
      <div class="container-fluid">
        <Link class="navbar-brand" to="/">{props.title}</Link>
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <ul class="navbar-nav me-auto mb-2 mb-lg-0">
            <div class="d-flex" role="search">
              <input class="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
              <button class="btn btn-outline-success" type="submit">Search</button>
            </div>



          </ul>
          <form class="d-flex" role="search">
            {!props.isloggedin && <Link to="/signup"><button class="btn btn-primary" type="submit">Signup</button></Link>}
            {props.isloggedin && <button onClick={logout} className="btn btn-danger">Logout</button>}
          </form>
        </div>
      </div>
    </nav>
  )
}

export default Navbar 